




























import uniStatusBar from './uni-status-bar.vue'
import uniIcon from './uni-icon.vue'

export default {
    components: {
        uniStatusBar,
        uniIcon
    },
    props: {
        /**
         * 
         */
        title: {
            type: String,
            default: ''
        },
        /**
         * 
         */
        leftText: {
            type: String,
            default: ''
        },
        /**
         * 
         */
        rightText: {
            type: String,
            default: ''
        },
        /**
         * 
         */
        leftIcon: {
            type: String,
            default: ''
        },
        /**
         * 
         */
        rightIcon: {
            type: String,
            default: ''
        },
        /**
         * 
         */
        fixed: {
            type: [Boolean, String],
            default: false
        },
        /**
         * 
         */
        color: {
            type: String,
            default: '#000000'
        },
        /**
         * 
         */
        backgroundColor: {
            type: String,
            default: '#FFFFFF'
        },
        /**
         * 
         */
        statusBar: {
            type: [Boolean, String],
            default: ''
        },
        /**
         * 
         */
        shadow: {
            type: String,
            default: ''
        }
    },
    computed: {
        isFixed() {
            return String(this.fixed) === 'true'
        },
        insertStatusBar() {
            switch (String(this.statusBar)) {
                case 'true':
                    return true
                case 'false':
                    return false
                default:
                    return this.isFixed
            }
        },
        hasShadow() {
            var backgroundColor = this.backgroundColor
            switch (String(this.shadow)) {
                case 'true':
                    return true
                case 'false':
                    return false
                default:
                    return backgroundColor !== 'transparent' && backgroundColor.indexOf('rgba') < 0
            }
        }
    },
    methods: {
        /**
         * ,
         */
        onClickLeft() { // 
            this.$emit('clickLeft') 
            this.$emit('click-left')
        },
        /**
         * 
         */
        onClickRight() {
            this.$emit('clickRight')
            this.$emit('click-right')
        }
    }
}



